let pagina = 1

const turno = {
    nombre: '',
    fecha: '',
    hora: '',
    servicios: []
}

document.addEventListener('DOMContentLoaded', () => {
    iniciarApp()
})

function iniciarApp() {
    // Reinicia el formulario
    reiniciarFormulario()

    // Muestra los servicios en el HTML
    mostrarServicios()

    // Resalta el div actual segun el tab que se presiona
    mostrarSeccion()

    // Oculta o muestra una seccion segun el tab que se presiona
    cambiarSeccion()

    // Paginacion siguiente y anterior
    paginaSiguiente()
    paginaAnterior()

    // Comprueba la pagina actual para ocultar o mostrar la paginacion
    botonesPaginador()

    // Muestra el resumen del turno o mensaje de error si no pasa la validacion
    mostrarResumen()

    // Almacena el nombre del turno en el objeto
    nombreTurno()

    // Almacena la fecha del turno en el objeto
    fechaTurno()

    // Almacena la hora del turno
    horaTurno()

    // Deshabilita dias pasados
    deshabilitarFechaAnterior()
}

function mostrarSeccion() {
    // Eliminar mostrar-seccion de la seccion anterior
    const seccionAnterior = document.querySelector('.mostrar-seccion')
    if (seccionAnterior) {
        seccionAnterior.classList.remove('mostrar-seccion')
    }

    const seccionActual = document.querySelector(`#paso-${pagina}`)
    seccionActual.classList.add('mostrar-seccion')

    // Eliminar la clase actual en el tab anterior
    const tabAnterior = document.querySelector('.tabs .actual')
    if (tabAnterior) {
        tabAnterior.classList.remove('actual')
    }

    // Resalta el tab actual
    const tab = document.querySelector(`[data-paso="${pagina}"]`)
    tab.classList.add('actual')
}

function cambiarSeccion() {
    const enlaces = document.querySelectorAll('.tabs button')

    enlaces.forEach(enlace => {
        enlace.addEventListener('click', e => {
            e.preventDefault()

            pagina = parseInt(e.target.dataset.paso)

            // Llamar la funcion de mostrar seccion
            mostrarSeccion()

            // Muestra u oculta los botones del paginador
            botonesPaginador()
        })
    });
}

async function mostrarServicios() {
    try {
        const resultado = await fetch('./servicios.json')
        const db = await resultado.json()
        const {servicios} = db

        // Generar el HTML
        servicios.forEach(servicio => {
            const {id, nombre, precio} = servicio

            // DOM scripting
            // Generar nombre de servicio
            const nombreServicio = document.createElement('p')
            nombreServicio.textContent = nombre
            nombreServicio.classList.add('nombre-servicio')

            // Generar el precio del servicio
            const precioServicio = document.createElement('p')
            precioServicio.textContent = `$${precio}`
            precioServicio.classList.add('precio-servicio')

            // Generar contenedor del servicio
            const servicioDiv = document.createElement('div')
            servicioDiv.classList.add('servicio')
            servicioDiv.dataset.servicioId = id

            // Selecciona un servicio para el turno
            servicioDiv.onclick = seleccionarServicio

            // Inyectar precio y nombre al contenedor del servicio
            servicioDiv.appendChild(nombreServicio)
            servicioDiv.appendChild(precioServicio)

            document.querySelector('#servicios').appendChild(servicioDiv)
        });
    } catch (error) {
        console.log(error)
    }
}

function seleccionarServicio(e) {
    // console.log(e.target.tagName)
    let elemento
    
    // Forzar el elemento al cual le damos click sea el div
    if (e.target.tagName === 'P') {
        elemento = e.target.parentElement
    } else {
        elemento = e.target
    }

    // Toggle class seleccionado
    if (elemento.classList.contains('seleccionado')) {
        elemento.classList.remove('seleccionado')

        const id = parseInt(elemento.dataset.servicioId)

        eliminarServicio(id)
    } else {
        elemento.classList.add('seleccionado')
        
        const servicioObj = {
            id: parseInt(elemento.dataset.servicioId),
            nombre: elemento.firstElementChild.textContent,
            precio: elemento.firstElementChild.nextElementSibling.textContent
        }

        agregarServicio(servicioObj)
    }

    // console.log(elemento.dataset.servicioId)
}

function eliminarServicio(id) {
    const {servicios} = turno
    turno.servicios = servicios.filter(servicio => servicio.id !== id)
    console.log(turno)
}

function agregarServicio(servicio) {
    const {servicios} = turno
    turno.servicios = [...servicios, servicio]
    console.log(turno)
}

function paginaSiguiente() {
    const btnSiguiente = document.querySelector('#siguiente')
    btnSiguiente.addEventListener('click', () => {
        pagina++
        botonesPaginador()
    })
}

function paginaAnterior() {
    const btnAnterior = document.querySelector('#anterior')
    btnAnterior.addEventListener('click', () => {
        pagina--
        botonesPaginador()
    })
}

function botonesPaginador() {
    const btnSiguiente = document.querySelector('#siguiente')
    const btnAnterior = document.querySelector('#anterior')

    if (pagina === 1) {
        btnAnterior.classList.add('ocultar')
        btnSiguiente.classList.remove('ocultar')
    }else if (pagina === 3) {
        btnSiguiente.classList.add('ocultar')
        btnAnterior.classList.remove('ocultar')
        mostrarResumen() // Carga el resumen del turno
    } else {
        btnAnterior.classList.remove('ocultar')
        btnSiguiente.classList.remove('ocultar')
    }

    mostrarSeccion()
}

function mostrarResumen() {
    // Destructuring
    const {nombre, fecha, hora, servicios} = turno

    // Seleccionar el resumen
    const resumenDiv = document.querySelector('.contenido-resumen')

    // Limpia el HTML previo
    while (resumenDiv.firstChild) {
        resumenDiv.removeChild(resumenDiv.firstChild)
    }

    // console.log(turno)

    // Validacion de objeto
    if (Object.values(turno).includes('')) {

        const noServicios = document.createElement('p')
        noServicios.textContent = 'Faltan datos de servicios, nombre, fecha u hora.'
        noServicios.classList.add('invalidar-turno', 'text-center')

        // Agregar a resumen div
        resumenDiv.appendChild(noServicios)

        return
    }

    const headingTurno = document.createElement('h3')
    headingTurno.textContent = 'Resumen de turno:'

    // Mostrar el resumen
    const nombreTurno = document.createElement('p')
    nombreTurno.innerHTML = `<span>Nombre:</span> ${nombre}`

    const fechaTurno = document.createElement('p')
    fechaTurno.innerHTML = `<span>Fecha:</span> ${fecha}`

    const horaTurno = document.createElement('p')
    horaTurno.innerHTML = `<span>Hora:</span> ${hora}`

    const serviciosTurno = document.createElement('div')
    serviciosTurno.classList.add('resumen-servicios')

    const headingServicios = document.createElement('h3')
    headingServicios.textContent = 'Resumen de servicios:'

    serviciosTurno.appendChild(headingServicios)

    let cantidad = 0

    // Iterar sobre el arreglo de servicios
    servicios.forEach(servicio => {
        const {nombre, precio} = servicio
        const contenedorServicio = document.createElement('div')
        contenedorServicio.classList.add('contenedor-servicio')
        
        const nombreServicio = document.createElement('p')
        nombreServicio.textContent = nombre
        nombreServicio.classList.add('precio')

        const precioServicio = document.createElement('p')
        precioServicio.textContent = precio

        const totalServicio = precio.split('$')
        cantidad += parseInt(totalServicio[1].trim())

        // Colocar texto y precio en el div
        contenedorServicio.appendChild(nombreServicio)
        contenedorServicio.appendChild(precioServicio)

        serviciosTurno.appendChild(contenedorServicio)
    });

    resumenDiv.appendChild(headingTurno)
    resumenDiv.appendChild(nombreTurno)
    resumenDiv.appendChild(fechaTurno)
    resumenDiv.appendChild(horaTurno)
    resumenDiv.appendChild(serviciosTurno)

    const cantidadPagar = document.createElement('p')
    cantidadPagar.classList.add('total')
    cantidadPagar.innerHTML = `<span>Total a pagar: </span>$${cantidad}`

    resumenDiv.appendChild(cantidadPagar)
}

function nombreTurno() {
    const nombreInput = document.querySelector('#nombre')
    nombreInput.addEventListener('input', e => {
        const nombreTexto = e.target.value.trim()

        // Validacion de que nombreTexto debe tener algo
        if (nombreTexto === '' || nombreTexto.length < 3) {
            mostrarAlerta('Nombre no válido.', 'error')
        } else {
            const alerta = document.querySelector('.alerta')
            if (alerta) {
                alerta.remove()
            }
            turno.nombre = nombreTexto
        }
    })
}

function fechaTurno() {
    const fechaInput = document.querySelector('#fecha')
    fechaInput.addEventListener('input', e => {
        const dia = new Date(e.target.value).getUTCDay()

        if ([0, 6].includes(dia)) {
            e.preventDefault()
            fechaInput.value = ''
            mostrarAlerta('Fines de semana no son permitidos.', 'error')
        } else {
            turno.fecha = fechaInput.value
        }
    })
}

function horaTurno() {
    const inputHora = document.querySelector('#hora')
    inputHora.addEventListener('input', e => {
        const horaTurno = e.target.value
        const hora = horaTurno.split(':')

        if (hora[0] < 10 || hora[0] > 21) {
            mostrarAlerta('Hora no válida', 'error')
            setTimeout(() => {
                inputHora.value = ''
            }, 3000);
            return
        }

        turno.hora = horaTurno
    })
}

function mostrarAlerta(mensaje, tipo) {

    // Si hay una alerta previa, no crear otra
    const alertaPrevia = document.querySelector('.alerta')

    if (alertaPrevia) {
        return
    }

    const alerta = document.createElement('div')
    alerta.textContent = mensaje
    alerta.classList.add('alerta')

    if (tipo === 'error') {
        alerta.classList.add('error')
    }

    // Insertar en el html
    const formulario = document.querySelector('.formulario')
    formulario.appendChild(alerta)

    // Eliminar la alerta despues de 3 segundos
    setTimeout(() => {
        alerta.remove()
    }, 3000);
}

function deshabilitarFechaAnterior() {
    const inputFecha = document.querySelector('#fecha')

    const fechaAhora = new Date()
    const year = fechaAhora.getFullYear()
    const mes = fechaAhora.getMonth() + 1
    const dia = fechaAhora.getDate() + 1

    // Formato deseado: AAAA-MM-DD
    const fechaDeshabilitar = `${year}-${mes}-${dia}`

    inputFecha.min = fechaDeshabilitar
}

function reiniciarFormulario() {
    const formulario = document.querySelector('.formulario')

    formulario.reset()
}